* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.set-timer-input {
  margin-right: 10px;

  background-color: rgba(#eee, 0.21);
  border: 2px solid rgba(#212121, 0.5);
}

.start-timer-btn {
  padding: 10px 20px;

  font-size: 1rem;
  color: inherit;

  background: none;
  background-color: tomato;
  border: none;
  appearance: none;
}

.start-timer-btn:focus,
.start-timer-btn:hover {
  color: rgba(#eee, 0.21);
  box-shadow: 0 0 0 35px rgba(#212121, 0.5);
}
